    import { Splide, SplideSlide, SplideTrack } from "@splidejs/react-splide";
    import { Card, Table, Button } from "react-bootstrap";
    import "../../assets/css/slide.scss";
    import React from "react";
    import { manageSpace } from "../../../../../utils";
    import { NoCameraImgPost } from "../../../../../actions/api";
    const TopeventSekleton = React.lazy(() =>
        import("../../layout/skeleton/TopeventSekleton")
      );

    const TopeventVoice = (props) => {
    const settings = {
        type: "slide",
        rewind: false,
        speed: 1000,
        loop: true,
        interval: 7000,
        drag: true,
        perPage: 4,
        gap: 5,
        autoplay: false,
        padding: "0",
        pagination: false,
        arrows: true,
    };

    const { topeventVoices, top_event_loader } = props;

    return (
        <>
        <Card className="sw-card-custom gutter-b card-shadow mb-5">
        <Card.Header className="px-5 pt-5 border-0 pb-0 d-flex align-items-center justify-content-between">
            <h6 class="card-label font-weight-bolder m-0">Top Event Voices</h6>
            { topeventVoices.total>6 ?  <Button variant="link" className="p-0">See More</Button> : null }
        </Card.Header>
          <Card.Body className="py-5 px-0">
            <Table className="mb-0" responsive>
            <thead>
                <tr>
                <th style={{ width: "10%" }}>
                    S No.
                </th>
                <th style={{ width: "20%" }}>
                    Name
                </th>
                <th style={{ width: "15%" }}>
                    No. of Posts
                </th>
                <th style={{ width: "15%" }}>
                    Social Accout
                </th>
                <th style={{ width: "30%" }}>
                    Recent Posts
                </th>
                <th style={{ width: "10%" }}></th>
                </tr>
            </thead>
            <tbody>
                { top_event_loader ? <TopeventSekleton/> : topeventVoices && Object.keys(topeventVoices.data).length
                ? topeventVoices.data.map((data,key) => {
                    return (
                        <tr className="border-top">
                            <td>
                                <strong>{key+1}</strong>
                            </td>
                        <td>
                            <div className="d-flex align-items-center">
                                <div className="symbol symbol-35 symbol-light me-3 rounded-circle overflow-hidden con_user_img cursor-pointer">
                                    <img src={data.image} className="img-fluid" width={"20px"} height={"20px"}/>
                                </div>
                                <div class="d-flex text-left flex-column conn_user_info cursor-pointer">
                                    <p class="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis author_name text-capitalize">
                                    {data.author.name}
                                    </p>
                                    <p class="m-0">
                                    <small class="text-muted overflow-hidden text-nowrap text-ellipsis">
                                       {`@${manageSpace(data.author.username)}`}
                                    </small>
                                    </p>
                                </div>
                            </div>
                        </td>
                        <td>
                            <strong>{data.postCount}</strong>
                        </td>
                        <td>
                            <i className={`fab ${data.network.icon} font-size-h2`}></i>
                        </td>
                        <td>

                         { data.postsData && Object.keys(data.postsData).length && data.postsData.filter(obj => obj.postFile!="").length ?                                 
                            <Splide
                            className="recent-post m-0"
                            hasTrack={false}
                            options={settings}
                            >
                            <SplideTrack>
                              { data.postsData.map((asset, index) => {
                                return (
                                    <SplideSlide key={index}>
                                       <img id={`post_${asset.postId}`} onError={e => e.target.src = NoCameraImgPost}
                                        src={asset.postFile}
                                        className="img-fluid"
                                        width={"20px"}
                                        height={"20px"}
                                    />
                                    </SplideSlide>
                                );
                                })}
                            </SplideTrack>
                            </Splide> : null }
                        </td>
                        <td>
                            <div className="conn_status_wrap">
                             <button onClick={() =>window.open(data.links)}
                                type="button"
                                className="btn btn-primary btn-min-w btn-sm rounded"
                            >
                                <span className="position-relative">
                                {" "}
                                <svg
                                    xmlns="http://www.w3.org/2000/svg"
                                    width="22.213"
                                    height="18"
                                    viewBox="0 0 22.213 18"
                                    className="h-75 align-self-end w-20 me-1"
                                >
                                    <g
                                    id="noun-talk-2902544"
                                    transform="translate(-98.007 -75.786)"
                                    >
                                    <path
                                        id="Path_6701"
                                        data-name="Path 6701"
                                        d="M103.686,84.417a1.084,1.084,0,1,1-.317-.766A1.084,1.084,0,0,1,103.686,84.417Zm2.9-1.084h0a1.084,1.084,0,1,0,.766.317,1.084,1.084,0,0,0-.766-.317Zm3.979,0h0a1.084,1.084,0,1,0,.766.317,1.084,1.084,0,0,0-.766-.317Zm9.659-5.711v8.172a1.839,1.839,0,0,1-1.616,1.823l.625,2.7a.617.617,0,0,1-1,.608l-3.07-2.625v.2a1.838,1.838,0,0,1-1.836,1.836h-9.463L100,93.638a.617.617,0,0,1-1-.608l.625-2.7A1.838,1.838,0,0,1,98.007,88.5V80.331a1.838,1.838,0,0,1,1.836-1.836h3.226v-.873a1.838,1.838,0,0,1,1.836-1.836h13.478a1.838,1.838,0,0,1,1.836,1.836Zm-6.3,2.709a.6.6,0,0,0-.6-.6H99.843a.6.6,0,0,0-.6.6V88.5a.6.6,0,0,0,.6.6h.552a.617.617,0,0,1,.6.756l-.375,1.621,2.607-2.229a.618.618,0,0,1,.4-.148h9.691a.6.6,0,0,0,.6-.6Zm5.063-2.709a.6.6,0,0,0-.6-.6H104.906a.6.6,0,0,0-.6.6V78.5h9.017a1.838,1.838,0,0,1,1.836,1.836v6.35l2.448,2.093-.375-1.621a.617.617,0,0,1,.6-.756h.553a.6.6,0,0,0,.6-.6Z"
                                        fill="#ffffff"
                                    ></path>
                                    </g>
                                </svg>{" "}
                                Connect{" "}
                                </span>
                            </button>
                            </div>
                        </td>
                        </tr>
                    );
                    })
                : null}
              
            </tbody>
            </Table>
  
        </Card.Body>
        </Card>
        </>
    );
    };

    export default React.memo(TopeventVoice);;
